@import "../../node_modules/normalize.css/normalize";

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

button {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
  display: block;
}

img {
  display: block;
  width: 100%;
}

body {
  font-family: Gilroy;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  width: 100%;
}

.main {
  height: 100%;
  background: url("../img/bg.jpeg") no-repeat center top / cover;

  > .container {
    height: 100%;
  }


}

.main__wrap {
  background: url("../img/wheel-bg.png") no-repeat center top / contain;
  height: 1054px;
  width: 1140px;
  position: relative;
}

.wheel {
  position: absolute;
  top: 243px;
  left: 300px;
  width: 520px;
  height: 520px;
  transition: transform 3s ease-in-out;
  z-index: 1;
}

.pointer {
  z-index: 2;
  position: absolute;
  width: 273px;
  top: 177px;
  left: 423px;
}

.btn {
  width: 173px;
  height: 149px;
  position: absolute;
  cursor: pointer;
  z-index: 3;
  top: 428px;
  left: 471px;
}

.copyright {
  position: absolute;
  left: 50%;
  bottom: 12px;
  transform: translateX(-54%);
}

.attempts {
  position: absolute;
  right: 208px;
  bottom: 300px;
  width: 83px;
  height: 83px;
  font-weight: 900;
  font-size: 45px;
  line-height: 83px;
  color: #fff;
  text-align: center;
}


.overlay {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(239, 223, 188, .7);
  &.active {
    display: block;
  }
}

.popup {
  display: none;
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 11;
  transform: translate3d(-50%, -50%, 0);
  width: 480px;
  &.active {
    display: block;
  }
}

.popup__coins {
  position: absolute;
  top: -51px;
  left: -50px;
  pointer-events: none;
  width: auto;
}

.popup__wrap {
  width: 410px;
  box-shadow: 0 13px 29px 0 rgba(0, 0, 0, .6);
  padding: 40px;
  margin: -10px auto 0;
  border-bottom-right-radius: 14px;
  border-bottom-left-radius: 14px;
  background-color: #fff;
  text-align: center;
}

.popup__header {
  position: relative;
  z-index: 5;
  background: url("../img/popup-head.png") no-repeat top center / cover;
  padding-top: 32px;
  padding-bottom: 5px;
  font-weight: 900;
  font-size: 42px;
  line-height: 64px;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  z-index: 1;
}

.popup__title {
  margin-bottom: 19px;
  font-weight: 600;
  font-size: 42px;
  line-height: 1;
  color: #e69200;
  letter-spacing: normal;
}

.popup__text {
  margin-bottom: 15px;
  font-weight: 300;
  font-size: 21px;
  line-height: 1;
  color: #000;
  letter-spacing: normal;

  b {
    font-weight: 900;
    font-size: 25px;
    color: #ff1f42;
  }
}

.popup__input {
  position: relative;
  margin-bottom: 18px;
  .error {
    display: none;
  }
}

.popup__input input {
  width: 100%;
  height: 41px;
  box-shadow: none;
  padding: 4px 15px 0;
  border: 2px solid #ff9c00;
  border-radius: 6px;
  font-size: 17px;
  color: #636363;
  margin-bottom: 0;
}

.error {
  font-size: 14px;
  line-height: 1;
  color: red;
  position: absolute;
  left: 0;
  bottom: 0;
}

.popup__btn {
  width: 100%;
  height: 37px;
  background: url("../img/popup-btn.png") no-repeat center / cover;
  padding: 0;
  border: none;
  border-radius: 18px;
  font-weight: 900;
  font-size: 17px;
  color: #fff;
  letter-spacing: normal;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
}

.onemore__btn {
  background: url("../img/onemore-btn.png") no-repeat center / cover;
  font-size: 25px;
  height: 60px;
  line-height: 60px;
  margin-top: 40px;
}

.popup__first {
  p {
    font-weight: 300;
    font-size: 21px;
    line-height: 1;
    color: #000;
    letter-spacing: normal;
  }

  .popup__text {
    margin-bottom: 35px;
    font-size: 33px;
    line-height: 1;
    color: #000;
    letter-spacing: normal;
    font-family: 'MyriadPro', sans-serif;
    b {
      font-family: Gilroy;
      font-weight: 900;
      color: #ff1f42;
      font-size: 33px;
    }
  }
}

.popup__second {

}

@media screen and (max-width: 1169px) {
  .main {
    min-width: 606px;
    padding-bottom: 143px;
    min-height: 100%;
    background: url(../img/bg-mob.jpeg) no-repeat center top / cover;
  }

  .main__wrap {
    background: url("../img/wheel-bg-mob.png") no-repeat center top / contain;
    width: 606px;
    height: 1307px;
    margin: 0 auto;
  }

  .wheel {
    top: 341px;
    left: 79px;
    width: 447px;
    height: 447px;
  }

  .attempts {
    right: 418px;
    bottom: 393px;
    width: 54px;
    height: 54px;
    font-size: 36px;
    line-height: 54px;
  }

  .pointer {
    top: 270px;
    left: 180px;
    width: 245px;
  }

  .btn {
    width: 154px;
    height: 138px;
    top: 495px;
    left: 223px;
  }

  .copyright {
    bottom: -90px;
    transform: translate3d(-50%, 0, 0);
    width: 190px;
  }
}

@media screen and (max-width: 606px) {
  body {
    overflow-x: hidden;
  }
  .main {
    min-width: 320px;
    padding-bottom: 143px;
  }

  .main__wrap {
    background: url("../img/wheel-bg-mob-2.png") no-repeat center top / contain;
    width: 310px;
    height: 667px;
  }

  .wheel {
    top: 174px;
    left: 40px;
    width: 229px;
    height: 229px;
  }

  .pointer {
    top: 134px;
    left: 88px;
    width: 133px;
  }

  .btn {
    width: 84px;
    top: 252px;
    left: 112px;
  }

  .attempts {
    right: 213px;
    bottom: 201px;
    width: 28px;
    height: 28px;
    font-size: 18px;
    line-height: 28px;
  }

  .popup__coins {
    top: -20px;
    left: -40px;
    width: 386px;
  }

  .popup__header {
    padding-top: 20px;
    font-size: 30px;
    line-height: 45px;
    background: url("../img/popup-head-mob.png") no-repeat top center / contain;
  }

  .popup {
    width: 320px;
  }

  .popup__title {
    margin-bottom: 10px;
    font-size: 30px;
  }

  .popup__wrap {
    width: 280px;
    padding: 10px;
    padding-bottom: 22px;
  }

  .popup__first {
    .popup__text {
      margin-bottom: 20px;
      font-size: 26px;
    }

    p {
      margin-bottom: 25px;
      font-size: 16px;
    }
  }

  .onemore__btn {
    margin-top: 0;
  }

  .popup__second {
    .popup__text {
      margin-bottom: 25px;
      font-size: 16px;
      b {
        font-size: 18px;
      }
    }
  }

}

@media screen and (max-width: 320px) {
  .popup__coins {
    display: none;
  }
  .main__wrap {
    width: 100%;
  }

  .popup {
    //width: 100%;
  }

  .wheel {
    top: 171px;
    left: 29px;
  }

  .pointer {
    top: 131px;
    left: 79px;
    width: 129px;
  }

  .btn {
    top: 248px;
    left: 102px;
  }

  .attempts {
    right: 203px;
    bottom: 205px;
  }

}